<template>
   <div class="address-view">
      <div class="pd-10-bottom">
         <h2 class="title">{{ internationalDict.address }}</h2>
      </div>
      <el-row type="flex" justify="start" class="sreach-bar" :gutter="20">
         <!-- <el-col :span="4">
            <el-input v-model="input" placeholder="請输入内容" clearable></el-input>
         </el-col> -->
         <el-col :xs="9" :sm="9" :md="4" :lg="4" :xl="6">
            <el-select v-model="value" placeholder="請選擇品牌" clearable>
               <el-option v-for="item in options" :key="item.value" :label="
                  internationalstate === 'english' ? item.labelEng : item.label
               " :value="item.value">
               </el-option>
            </el-select>
         </el-col>
         <el-col :xs="15" :sm="15" :md="12" :lg="12" :xl="12">
            <el-checkbox-group v-model="checkList">
               <el-checkbox v-for="(item, index) of internationalDict.addressList" :key="index" :label="index">{{
                  item
               }}
               </el-checkbox>
            </el-checkbox-group>
         </el-col>
      </el-row>
      <div class="address-box">
         <el-row class="full-height" :class="windowSize === 'mobile' ? 'mobile-address-box' : ''" :gutter="20">
            <el-col class="address-container" :xs="24" :sm="12" :md="12" :lg="12" :xl="12">
               <ul class="address-list">
                  <li v-for="(item, index) of addressList" :key="index" class="address-card" @click="flytoShop(item)">
                     <el-card>
                        <div class="full-height">
                           <h3 v-if="internationalstate === 'chinese'" class="pd-5-bottom">
                              {{ item.name_cn }}
                           </h3>
                           <h3 v-if="internationalstate === 'english'" class="pd-5-bottom">
                              {{ item.name_eng }}
                           </h3>
                           <div class="flex-row-left">
                              <div class="address-detail">
                                 <p v-if="internationalstate === 'chinese'">
                                    <i class="el-icon-s-home icon"></i>{{ item.address_cn }}
                                 </p>
                                 <p v-if="internationalstate === 'english'">
                                    <i class="el-icon-s-home icon"></i>{{ item.address_eng }}
                                 </p>
                                 <p><i class="el-icon-phone icon"></i>{{ item.phone }}</p>
                              </div>
                              <div class="btn-group" v-if="item.googleLink">
                                 <el-button plain type="primary" round @click="openLink(item.googleLink)" size="mini">{{
                                    internationalDict.takeOut
                                 }}</el-button>
                              </div>
                           </div>
                        </div>
                     </el-card>
                  </li>
               </ul>
            </el-col>
            <el-col class="map-container" :xs="24" :sm="12" :md="12" :lg="12" :xl="12" :style="{height:windowSize === 'mobile' ? '50%' : '100%'}">
               <div id="map"></div>
            </el-col>
         </el-row>
      </div>
   </div>
</template>
<script>
import mapboxgl from "mapbox-gl";
import { shopAddress } from "../assets/googleFormData/apiData.js";
export default {
   name: "address-view",
   components: {},
   mixins: [],
   props: {},
   data() {
      return {
         GMap: null,
         timeoutHandle:null,
         input: "",
         value: "",
         options: [
            {
               value: 0,
               label: "616牛肉火鍋專門店",
               labelEng: "616 Beef Hotpot",
            },
            {
               value: 2,
               label: "616燒肉酒場",
               labelEng: "616 YanKiniku",
            },
            {
               value: 3,
               label: "616客香邨",
               labelEng: "616 HakKaInn",
            },
            {
               value: 4,
               label: "虎喰燒肉酒場",
               labelEng: "Tora Kui Japanese Yakiniku",
            },
         ],
         checkList: [0, 1, 2, 3],
         shopList: [],
      };
   },
   computed: {
      addressList() {
         let res = this.shopList.filter((item) => {
            console.log(this.checkList.includes(Number(item.district)));
            return this.checkList.includes(Number(item.district));
         });
         if (this.input) {
            var reg = new RegExp(this.input);
            res = res.filter((item) => item.name.match(reg));
         }
         if (this.value !== "") {
            res = res.filter((item) => item.type === this.value);
         }
         return res;
      },
      internationalDict() {
         return this.$store.getters.getInternationalDict;
      },
      internationalstate() {
         return this.$store.getters.getInternationalstate;
      },
      windowSize() {
         return this.$store.getters.getWindowSize;
      }
   },
   watch: {},
   mounted() {
      this.getAddressList();
      this.initMap();
   },
   methods: {
      openLink(link) {
         window.open(link);
      },
      initMap() {
         mapboxgl.accessToken =
            "pk.eyJ1Ijoia2F5ZWxhdSIsImEiOiJjbDVxM3h6dDIxc3dmM2Vud2pmMjBzbGFzIn0.iDb2G_R5ITAVkm_lN0A37g";
         this.GMap = new mapboxgl.Map({
            container: "map",
            // Choose from Mapbox's core styles, or make your own style with Mapbox Studio
            style: "mapbox://styles/mapbox/streets-v12",
            center: [114.1738,22.4045],
            zoom: 9.7,
         });
      },
      getAddressList() {
         const sheetsId = "1qm9P5CZYojslr10ltiNfqYSNOb4mMRBlyrhUO8Lreks";
         const sheet = "sheet1";
         const apiKey = process.env.VUE_APP_GOOGLEKEY;
         let timeoutHandle = setTimeout(() => {
            this.shopList = shopAddress;
            this.loadPoint()
         }, 1500);
         fetch(
            `https://sheets.googleapis.com/v4/spreadsheets/${sheetsId}/values/${sheet}?key=${apiKey}`
         )
            .then((response) => {
               return response.json();
            })
            .then((res) => {
               if (!res.values || !res.values.length) return;
               clearTimeout(timeoutHandle);
               const header = res.values[0];
               const content = res.values.map((item) => {
                  let value = {};
                  header.forEach((cell, index) => {
                     if (cell === "type" || cell === "district") {
                        value[cell] = Number(item[index]);
                     } else {
                        value[cell] = item[index];
                     }
                  });
                  return value;
               });
               this.shopList = content.slice(1);
               this.loadPoint()
            })
            .catch((err) => {
               console.error(err)
               // this.shopList = shopAddress;
               // this.loadPoint()
            });
      },
      loadPoint(){
         this.shopList.forEach((item) => {
            if(item.longitude && item.latitude){
               new mapboxgl.Marker()
               .setLngLat([item.longitude, item.latitude])
               .addTo(this.GMap);
            }
         })
      },
      flytoShop(row){
         this.GMap.flyTo({
            zoom: 14,
            speed: 1,
            center: [row.longitude, row.latitude],
            essential: true
         });
      }
   },
};
</script>
<style scoped>
.address-view {
   height: 85vh;
   overflow: hidden;
   margin-bottom: 2em;
}

.address-detail {
   flex: 1;
}

.address-detail>p+p {
   padding-top: 5px;
}

.btn-group {
   margin: auto;
}

.sreach-bar {
   padding-left: 15px;
   align-items: center;
}

.address-box {
   padding: 8px 15px 0;
   height: 100%;
   overflow: hidden;
}

.address-container {
   padding: 5px 0;
   height: 100%;
   overflow-y: scroll;
}
.mobile-address-box{
   display: flex;
   flex-direction: column-reverse;
}

.address-container::-webkit-scrollbar {
   display: none;
}
.map-container{
   height: 100%;
}
.address-list {
   padding-bottom: 150px;
   width: 100%;
   list-style: none;
}
.address-card{
   cursor: pointer;
}

.address-list>li {
   padding-bottom: 5px;
   display: flex;
   flex-direction: column;
   gap: 5px;
}

.address-list>li>h3 {
   color: var(--primary-color);
}

.icon {
   color: var(--primary-color);
   margin-right: 5px;
}

#map {
   height: 100%;
   width: 100%;
}
</style>
